


function getSiHuaStyle(pan, siHua) {
  var tmp;
  switch (pan) {
    case /* YuanJu */0 :
        switch (siHua) {
          case /* Lu */0 :
              tmp = "yuanju lu";
              break;
          case /* Quan */1 :
              tmp = "yuanju quan";
              break;
          case /* Ke */2 :
              tmp = "yuanju ke";
              break;
          case /* Ji */3 :
              tmp = "yuanju ji";
              break;
          
        }
        break;
    case /* XianPan */1 :
        switch (siHua) {
          case /* Lu */0 :
              tmp = "xian lu";
              break;
          case /* Quan */1 :
              tmp = "xian quan";
              break;
          case /* Ke */2 :
              tmp = "xian ke";
              break;
          case /* Ji */3 :
              tmp = "xian ji";
              break;
          
        }
        break;
    case /* LiuYearPan */2 :
        switch (siHua) {
          case /* Lu */0 :
              tmp = "year lu";
              break;
          case /* Quan */1 :
              tmp = "year quan";
              break;
          case /* Ke */2 :
              tmp = "year ke";
              break;
          case /* Ji */3 :
              tmp = "year ji";
              break;
          
        }
        break;
    case /* LiuMonthPan */3 :
        switch (siHua) {
          case /* Lu */0 :
              tmp = "month lu";
              break;
          case /* Quan */1 :
              tmp = "month quan";
              break;
          case /* Ke */2 :
              tmp = "month ke";
              break;
          case /* Ji */3 :
              tmp = "month ji";
              break;
          
        }
        break;
    case /* LiuDayPan */4 :
        switch (siHua) {
          case /* Lu */0 :
              tmp = "day lu";
              break;
          case /* Quan */1 :
              tmp = "day quan";
              break;
          case /* Ke */2 :
              tmp = "day ke";
              break;
          case /* Ji */3 :
              tmp = "day ji";
              break;
          
        }
        break;
    
  }
  return "sihua " + tmp;
}

export {
  getSiHuaStyle ,
  
}
/* No side effect */
